.header .logo-img {
  padding-top: 27px;
  width: 203px; }

.header .hamburger {
  position: fixed;
  top: 27px;
  right: 20px; }
  .header .hamburger span {
    display: flex;
    height: 5px;
    width: 40px;
    background-color: #c4c4c4;
    margin-bottom: 4px;
    transition: 0.5s all;
    border-radius: 20px; }
  .header .hamburger--active span:nth-child(1) {
    transform: translateY(8px) rotate(45deg); }
  .header .hamburger--active span:nth-child(2) {
    display: none; }
  .header .hamburger--active span:nth-child(3) {
    transform: translateY(-1px) rotate(-45deg); }

.header .menu {
  font-family: 'Gilroy', serif;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0px;
  left: -100%;
  width: 80%;
  height: 100vh;
  padding: 0 20px;
  background-color: white;
  transition: 0.6s all;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.25);
  z-index: 2; }
  .header .menu--active {
    left: 0; }
  .header .menu-socials {
    display: none; }
  .header .menu-list {
    list-style-type: none;
    margin-top: 64px;
    font-family: 'Gilroy', sans-serif; }
    .header .menu-list__item {
      padding: 15px 0; }
      .header .menu-list__item:last-child {
        padding-bottom: 0; }
    .header .menu-list__link {
      font-family: 'Gilroy', sans-serif;
      color: #222831;
      font-size: 16px;
      font-weight: 800; }
      .header .menu-list__link-dropdown {
        font-family: 'Gilroy', sans-serif;
        color: #222831;
        font-size: 16px;
        font-weight: 800; }
        .header .menu-list__link-dropdown::after {
          content: '▼';
          color: #3F72AF;
          padding-left: 20px; }
  .header .menu .sub-menu__list {
    list-style-type: none;
    display: none; }
    .header .menu .sub-menu__list-link {
      color: #222831; }
  .header .menu .e-mail {
    display: none; }
  .header .menu .phone {
    margin-top: 80px;
    align-items: flex-start; }
  .header .menu .bullets {
    display: none; }

.header .phone {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  text-align: right; }
  .header .phone-link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 24px;
    color: black; }
  .header .phone-workhours {
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;
    padding: 2px 0 6px 0; }

@media (min-width: 375px) {
  .header .logo-img {
    width: 232px; } }

@media (min-width: 768px) {
  .header .menu {
    width: 50%; } }

@media (min-width: 1024px) {
  .header .logo {
    display: none; }
  .header .phone {
    display: none; }
  .header .hamburger {
    display: none; }
  .header .menu {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    padding: 0;
    background-color: transparent;
    transition: 0.6s all;
    box-shadow: none;
    z-index: 2; }
    .header .menu-list {
      display: flex;
      justify-content: space-between;
      flex-basis: 85%;
      border-bottom: 1px solid #868686;
      margin-top: 0; }
      .header .menu-list__item {
        padding: 19px 0 19px 0;
        transition: 0.5s all; }
        .header .menu-list__item:last-child {
          padding-bottom: 28px; }
        .header .menu-list__item:first-child {
          margin-left: 10%; }
        .header .menu-list__item:hover {
          border-bottom: 2px solid #3F72AF; }
    .header .menu-socials {
      display: flex;
      flex-basis: 15%;
      justify-content: space-around;
      font-size: 25px;
      padding: 19px 0 16px 0;
      border-bottom: 1px solid #868686; }
      .header .menu-socials__link {
        color: #848484; }
    .header .menu .logo {
      display: flex;
      order: 2; }
      .header .menu .logo-img {
        width: 203px;
        padding-top: 25px; }
    .header .menu .e-mail {
      display: flex;
      order: 3;
      color: #222831;
      font-size: 16px;
      font-weight: 500;
      transition: 0.5s all; }
      .header .menu .e-mail:hover {
        color: #3F72AF; }
    .header .menu .phone {
      display: flex;
      order: 4;
      margin-top: 0;
      padding-top: 32px; } }

@media (min-width: 1440px) {
  .header .menu .bullets {
    display: flex;
    order: 3;
    flex-basis: 40%; }
    .header .menu .bullets-item:last-child {
      margin-top: 0; } }

.promo {
  background: url("../../images/header/header-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  .promo-title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    color: #3F72AF;
    margin-top: 25px;
    font-size: 30px; }
  .promo-subtitle {
    font-family: 'Montserrat', sans-serif;
    color: #222831;
    font-size: 15px;
    margin-top: 15px;
    font-weight: 400; }
  .promo-description {
    position: relative;
    font-family: 'Gilroy', sans-serif;
    font-size: 14px;
    font-weight: 900;
    margin-top: 42px;
    padding-bottom: 73px; }
    .promo-description::before {
      position: absolute;
      content: url("../../images/promo/gift.png");
      top: -40px;
      right: -20px; }
    .promo-description::after {
      position: absolute;
      content: url("../../images/promo/gift-2.png");
      top: 55px;
      left: -20px; }

.bullets {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: space-between;
  padding: 33px 20px 0 20px; }
  .bullets-item {
    display: flex;
    flex-direction: column; }
    .bullets-item:last-child {
      margin-top: 15px; }
    .bullets-item__achievement {
      position: relative;
      font-size: 24px;
      font-weight: 800;
      font-family: 'Gilroy', sans-serif; }
      .bullets-item__achievement:before {
        position: absolute;
        top: -1px;
        left: -20px;
