        color: #17B5F9;
        width: 8px;
        height: 8px;
        border-radius: 50%; }
      .bullets-item__achievement-description {
        font-size: 16px;
        font-family: 'Gilroy', sans-serif;
        font-weight: 300; }

@media (min-width: 1024px) {
  .promo {
    background: url("../../images/header/header-desktop-bg.jpg");
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    top: 0; }
    .promo .bullets {
      padding-top: 200px; }
      .promo .bullets-item:first-child {
        margin-left: 20px; }
      .promo .bullets-item:last-child {
        margin-top: 0px; } }

@media (min-width: 1440px) {
  .promo .bullets {
    display: none; }
  .promo-title {
    font-size: 50px; }
  .promo-subtitle {
    font-size: 50px;
